<template>
   <div class="Home">
    <app-header></app-header>
    <main class="main">
     
        <div class="game-list" id="games-list" ref="gamesList">
            <div class="game-item" 
                v-for="(item, indexPost ) in gamePosts"
                :style="{ 
                  height: gameHeight+'px', 
                  backgroundImage: 'url('+item.backgroundImage+')',
                  backgroundPosition : item.backgroundPosition.x+'%'+' '+ item.backgroundPosition.y+'%' 
                }"
                ref="game"
               @mouseenter="hiddenWindowEnter"
               @mouseleave="hiddenWindowLeave"
                
            >
              <div class="game-item__background">
                  <div class="caption">
                    <a class="caption__a">{{item.name}}</a>
                  </div>
                  
                  <hr>
                  <div class="genre">
                    <p class="genre-list"><span 
                        class="genre-item"
                        v-for="(genreItem,index) in item.genre"
                      >{{genreItem +','}} </span>
                    </p>
                  </div>
                    
              </div>
              <div class="hidden-window game-item__hidden-window"
                
               
              >
              <!-- @touchstart.self="hiddenWindowOver"
               "@mouseleave="hiddenWindowLeave"
                @touchend.self="hiddenWindowLeave" -->
                  <div class="caption caption--white-background">
                      <a class="caption__a">{{item.name}}</a>
                  </div>
                  <div class="btn-container">
                          <div class="btn ">
                              <input class="btn__input btn--pass" 
                                  type="submit" value="Страница игры" 
                                  @click="$router.push(`/game/:${indexPost}`)"
                              >
                          </div>
                          <div class="btn ">
                              <input class="btn__input btn--sub-link" 
                              type="submit" value="Моды">
                              <div class="count btn-count">
                                  1000
                              </div>
                          </div>
                          <div class="btn ">
                              <input class="btn__input btn--sub-link" 
                              type="submit" value="Статьи">
                              <div class="count btn-count">
                                  1000
                              </div>
                          </div>
               
                  </div>
                  
                 

              </div>
            </div>
  
        
        </div>
    </main>
  </div> 
</template>

<script>
    // let test =document.querySelectorAll('.game');

import { ref } from "vue";
import  GameListData  from "@/mixins/GameListData";


export default {
  mixins :[GameListData],
  data(){
    
    return {
      // gamesPostColl_on: ref(Date.now()),
      gameHeight : 0,
      
      window: {
        width: 0,
        height: 0
      },
      gamePost : document.querySelector('.game'),
    }
  },      
 

    
    
  // },
  methods: {
    handleResize(el) {
          // console.log(el);
          this.gameHeight = el; 
    },
    hiddenWindowEnter(event){
      console.log(event.target.querySelector('.game-item__hidden-window'));
      const btnInput = event.target.querySelectorAll('.btn__input');
      
      setTimeout(() => {
        btnInput.forEach(element => {
          element.classList.add('hover');
        });

      }, 800);
     
      event.target.querySelector('.game-item__hidden-window').classList.add('show');

    },
    hiddenWindowLeave(event){
      // console.log(event);
      const btnInput = event.target.querySelectorAll('.btn__input');

      setTimeout(() => {
        btnInput.forEach(element => {
          element.classList.remove('hover');
        });

      }, 800);


      event.target.querySelector('.game-item__hidden-window').classList.remove('show');

    }
  },
  computed:{
    
  },
  created() {
   

    
  },
  destroyed() {
      window.removeEventListener('resize', () =>{
        this.handleResize(this.$refs.game[0].clientWidth);
      },true);
  },
  mounted(){
      window.addEventListener('resize', () =>{
        this.handleResize(this.$refs.game[0].clientWidth);
      },true);
    this.handleResize(this.$refs.game[0].clientWidth);
 
  }


 
}
</script>

 <style lang="css">
*{
    -moz-box-sizing: border-box;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      padding: 0;
      margin: 0;
      font-family: 'Raleway', sans-serif;
  }
  /* #app {
   
  } */
 

  main{
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    padding-left: 300px;
    z-index: 1;
    background: #da9a9a;
    clear: both;

  }
  .game-list{
    display: block;
    width: 100%;
    height: 100%;
   
  }
  .game-item{
    display: flex;
    width: 33.33333333333333%;
    height: auto;
    float: left;
    position: relative;
    overflow: hidden;
     background-position: 50% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    align-items: flex-end;
  }
  
  :root {
    --background-opacity: #2c3e5070;
    --background: #454545;
  }
  .game-item__background{
    position: relative;
    width: 100%;
    height: max-content;

    padding-bottom: 2%;
    display: flex;
    flex-direction: column;
    align-items: center;
    
      
      background-color: var(--background-opacity);
      box-shadow: 0 0 40px 32px var(--background-opacity) ;
  }
  .game-item .caption{
    text-align: center;
    height: auto;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
  }
  .game-item .caption__a{
    width: 80%;
    position: relative;
    font-size: 1.5vw;
    color: #fff;
    text-shadow: 1px 1px 2px #557d7e;
    text-decoration: none;

  }
  .game-item__background hr{

    display:block;
    width: 50%;               
    margin: 5px 0 8px 0;
    background-color: #fff;
    height: 1px;
    box-shadow: 1px 1px 3px #557d7e;
  }
 
  .game-item__background .genre{
    width: 100%;
    height: 3vw;
  } 
  .game-item__background  .genre-list{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    
    color: #fff;
 
  }
  .game-item__background  .genre-item{
    display: flex;
    width: max-content;
    margin-bottom: 6px;
    margin-right: 5px;
    font-size: 0.9vw;
    text-shadow: 1px 1px 4px #557d7e;
    
  }

  .game-item__hidden-window{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
      background-color: #fff;
      opacity: 0;
      transition: opacity cubic-bezier(0.55, -0.03, 0, 1.8) 0.5s;
      z-index: 5;
  }
  .game-item__hidden-window.show{
    opacity: 1;
  }
  .caption--white-background {
    padding-top: 8%;
  }
  .caption--white-background .caption__a{
    color: #454545;
    text-shadow: none;


  }
  .btn-container{
    width: 80%;
    display: flex;
      flex-direction: column;
  }
  .game-item .btn{
    margin-top: 1.5vw;
    
    width: 100%;
    
    display: flex;
    justify-content: start;
  }
  
  .game-item .btn__input{
    width: max-content;
    height: max-content;
    background: none;
    border: none;
    text-align:  center;
    border: 2px solid #454545;
    outline: none;
   
    padding: 8px 12px;
    cursor: pointer;
    border-radius: 2px;
    font-size: 1.2vw;
    transition: background-color ease-in-out 0.3s;
  }

  .game-item .btn__input.hover:hover {
      background-color: var(--background);
      color: #fff;
  }

  .game-item .btn-count{
    margin-left: -2px;
    padding: 8px 12px;
    
    font-size: 1.2vw;
    /* border: 2px solid #454545;
    border-radius: 2px; */
  }
  .game-item .btn--pass {
    width: 100%;
    
  }
  .game-item .btn--sub-link{
    /* width:45%; */
    min-width: 40%;
    width: 45%;

    
  }
  @media screen and (max-width: 1100px) {
    header{
      display: none;
    }
    main{
      padding-left: 0px;
    }
    .game-item{    
      width: 50%;

    }
    .game-item__background {
     
     padding-bottom: 10%;
     
   }
   .game-item .caption__a {

     width: 80%;

     font-size: 4vw;
  
   }
   .game-item__background hr {
    
     width: 70%;
     
   }
   .game-item__background .genre-item {
    
     font-size: 2vw;
   }

   .game-item .btn__input {
    padding: 18px 20px;
    font-size: 2.2vw;
  }
  .game-item .btn-count {
    margin-left: -2px;
    padding: 18px 20px;
    font-size: 2.2vw;
}
  }
  @media screen and (max-width: 601px) {
    header{
      display: none;
    }
    main{
      padding-left: 0px;
    }
    .game-item{    
      width: 100%;

    }
    .game-item__background {
     
      padding-bottom: 10%;
      
    }
    .game-item .caption__a {

      width: 80%;

      font-size: 8vw;
   
    }
    .game-item__background hr {
     
      width: 70%;
      
    }
    .game-item__background .genre-item {
     
      font-size: 3.5vw;
    }

  }
</style> 